import React, { useState, useEffect } from "react";
import styles from "./layout.module.css";
import NavBar from "../navbar";
import SocialMediaIcons from "../socialMediaIcons";
import { TransitionPortal } from "gatsby-plugin-transition-link";
import NavHeader from "../navHeader";
import { HOME, ABOUT, BLOG, CONTACT } from "../../utils/constants";
import Helmet from "react-helmet";
import font1 from "../../../static/fonts/ZegoeCapsBold.ttf";
import font2 from "../../../static/fonts/ZegoeLight-U.ttf";
import font3 from "../../../static/fonts/ZegoeUI-U.ttf";
import font4 from "../../../static/fonts/ZegoeUI-UBold.ttf";
import font5 from "../../../static/fonts/ZegoeUISemiBold-U.ttf";
import "./animation.css";

// Wraps every page with header, navbar, and footer
// Main contains actual page components
const Layout = ({ children, path, location, title }) => {
  return (
    <>
      <Helmet>
        <link rel="preload" as="font" href={font1} crossOrigin="anonymous" />
        <link rel="preload" as="font" href={font2} crossOrigin="anonymous" />
        <link rel="preload" as="font" href={font3} crossOrigin="anonymous" />
        <link rel="preload" as="font" href={font4} crossOrigin="anonymous" />
        <link rel="preload" as="font" href={font5} crossOrigin="anonymous" />
      </Helmet>

      <div className={styles.layout}>
        {/* header fades in on initial load for home page and fades out for other page transitions */}
        <header
          className={path === HOME ? "headerFadeIn" : "headerFadeOut"}
          style={{ opacity: path === HOME ? 1 : 0 }}>
          <div className={styles.headerText}>
            <h3>John (Alex)ander Ferguson</h3>
            {/* TODO: add animation of different titles here */}
            <h6>full stack web developer</h6>
          </div>
          <SocialMediaIcons />
        </header>

        <TransitionPortal level="center">
          <NavBar centerStyle={styles.centerNav} pathname={location.pathname} />
        </TransitionPortal>

        {/* transition portal should be included here to allow fadeOut transitions from navigating backwards */}
        <main
          className={`${path !== HOME && "fadeIn"}`}
          style={{ opacity: path !== HOME ? 1 : 0 }}>
          {children}
        </main>

        <footer>
           {new Date().getFullYear()}, Built with
          {` `}
          <a href="https://www.gatsbyjs.org">Gatsby</a>
        </footer>
      </div>
    </>
  );
};

export default Layout;
